.header{
    display: flex;
    position: sticky;
    top: 0;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 15px 9vw;
    background-color: #ffffff9a;
    backdrop-filter: blur(5px);
    z-index: 9999;
}
.header img{
    width: 160px;
    height: auto;
}
.header button{
    background: #3857FF;
    color: white;
    padding: 10px 18px;
    border: 0;
    border-radius: 50px;
}